body {
    margin: 0;
    padding: 0;
    background-color: rgb(48, 48, 48);
}

a {
    text-decoration: none;
}

.dashboard-listing-header {
    background-color: #353c48;
    color: white;
    float: left;
    width: 100%;
}

.dashboard-listing-h1 {
    padding: 10px;
    margin: 0;
}

.dashboard-listing-header-logo {
    margin-right: 10px;
}

.dashboard-view {
    padding: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: calc(100vh - 64px);
}

.dashboard-view-full-width {
    margin-left: 0;
    height: 88vh;
}

.pages-navigation-panel {
    width: 14%;
    height: 100%;
    background-color: #353c48;
}

.pages-nav-panel-product-logo {
    text-align: center;
    font-size: 50px;
}

.pages-nav-panel-dashboard-name {
    text-align: center;
    font-size: 25px;
    margin-bottom: 25px;
}

.dashboard-view-header {
    background-color: #353c48;
    color: white;
    height: 60px;
    margin-bottom: 1%;
    margin-left: 0;
}

.dashboard-view-header-toggled {
    background-color: #353c48;
    color: white;
    height: 60px;
    margin-bottom: 1%;
    margin-left: 14%;
}

.pages-nav-toggle-btn {
    float: left;
    font-size: 30px;
    background: #438cad;
}

.dark-light-theme-switch-div {
    display: -ms-flexbox;
    display: flex;
    margin-left: 15px;
}

.dark-light-theme-switch-toggleBtn {
    margin-left: 10px;
}

.dashboard-view-product-logo {
    text-align: center;
    font-size: 50px;
    margin-bottom: 15px;
    padding-top: 24px;
}

.dashboard-view-product-name {
    text-align: center;
    font-size: 18px;
    margin-bottom: 10px;
}

.content-drawer-closed {
    padding-left: 0;
}

.content-drawer-opened {
    padding-left: 256px;
}

/* dashboard listing grid */
.dashboard-listing-container {
    padding: 10px;
}

.dashboard-thumbnail {
    width: 50%;
    float: left;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: Roboto, sans-serif;
}

.dashboard-thumbnail .content {
    background: #444; 
    padding: 15px;
    margin: 10px;
}

.dashboard-thumbnail h4, .dashboard-thumbnail p {
    margin: 0;
    padding: 0;
}

.dashboard-thumbnail h4 {
    font-size: 24px;
    font-weight: normal;
    margin-bottom: 5px;
}

.dashboard-thumbnail p {
    font-size: 14px;
}

.dashboard-thumbnail .actions {
    padding-top: 15px
}

.dashboard-thumbnail .actions a {
    color: rgb(48, 48, 48);
}

.dashboard-thumbnail .actions .icon {
    margin-right: 5px;
}

.dashboard-thumbnail .actions a.disabled {
    opacity: .6;
    pointer-events: none;
    margin-left: 10px;
}

.form {
    z-index: 15;
    position: relative;
    background: #FFFFFF;
    width: 600px;
    border-radius: 4px;
    -webkit-box-shadow: 0 0 30px;
            box-shadow: 0 0 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 100px auto 10px;
    overflow: hidden;
}
   .form-group {
       display: -ms-flexbox;
       display: flex;
       -ms-flex-wrap: wrap;
           flex-wrap: wrap;
       -ms-flex-pack: justify;
           justify-content: space-between;
       margin: 0 0 20px;
   }
    .form-group-label {
        display: block;
        margin: 0 0 10px;
        color: rgb(128,128,128);
        font-size: 12px;
        text-transform: uppercase;
        letter-spacing: .2em;
    }
    .form-group-input {
        outline: none;
        display: block;
        width: 100%;
        border: 0;
        border-radius: 4px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        padding: 12px 20px;
        color: rgb(220,220,220);
        font-family: inherit;
        font-size: inherit;
        line-height: inherit;
        -webkit-transition: 0.3s ease;
        -o-transition: 0.3s ease;
        transition: 0.3s ease;
    }
    .form-group-button {
        outline: none;
        background: #4285F4;
        width: 100%;
        border: 0;
        border-radius: 4px;
        padding: 12px 20px;
        color: #FFFFFF;
        font-family: inherit;
        font-size: inherit;
        font-weight: 500;
        line-height: inherit;
        text-transform: uppercase;
        cursor: pointer;
    }
.form-panel {
     padding: 60px calc(5% + 60px) 60px 60px;
     -webkit-box-sizing: border-box;
             box-sizing: border-box;
 }
.my-h1 {
    color: white;
    text-align: center;
}
.floating-button{
    margin-top: 2%;
    margin-left: 43cm;
}

.active-apps{
    color: green;
    display: inline;
}
.inactive-apps{
    color: red;
    display: inline;
}



